<template>
	<div class="container">
		<v-back title="推送详情" :show="false" />
		<div class="push_scroll">
			<div>
				<img v-if="$route.query.snapPhoto" :src="$route.query.snapPhoto" alt="" @click="imgsShow">
				<img v-else src="@/assets/student.png" alt="">
			</div>
			<div class="flex align_center">
				<div class="label">用户类型</div>
				<div class="content">{{usertypeName[$route.query.usertype] || '--'}}</div>
			</div>
			<div class="flex align_center">
				<div class="label">进出时间</div>
				<div class="content">{{$route.query.entryExitTime || '--'}}</div>
			</div>
			<div class="flex align_center">
				<div class="label">进出类型</div>
				<div class="content">{{entryExitTypeName($route.query.entryExitType)}}</div>
			</div>
			<div class="flex align_center">
				<div class="label">进出楼栋名称</div>
				<div class="content">{{$route.query.EntryExitBuildingName || '--'}}</div>
			</div>
			<div class="flex align_center">
				<div class="label">抓拍相机名称</div>
				<div class="content">{{$route.query.snapCameraName || '--'}}</div>
			</div>
		</div>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
	</div>
</template>

<script>
	export default {
		name: 'push_index',
		data() {
			return {
				usertypeName: {
					I: '陌生人',
					Y: '黄名单'
				},
				isViewerShow: false,
				imgs: []
			}
		},
		methods: {
			entryExitTypeName(type) {
				if (type == 1) {
					return '进'
				} else if (type == 2) {
					return '出'
				} else {
					return '--'
				}
			},
			imgsShow() {
				this.isViewerShow = true
				this.imgs = [this.$route.query.snapPhoto]
			}
		}
	}
</script>

<style scoped="scoped">
	.push_scroll {
		box-sizing: border-box;
		height: calc(100vh - 14vw);
		overflow-y: auto;
		padding: 4vw;
		font-size: 4vw;
	}
	.label {
		width: 30vw;
		text-align: right;
		margin-right: 4vw;
		padding: 3vw 0;
	}
	.content {
		font-weight: bolder;
	}
	img {
		max-height: 50vw;
		display: block;
		margin: 0 auto 3vw;
	}
</style>
